import {Component, OnInit, ViewChild} from '@angular/core';
import {BsModalRef, BsModalService} from 'ngx-bootstrap/modal';
import {HttpClient} from '@angular/common/http';
import {TranslateService} from '@ngx-translate/core';
import {ExportAsConfig, ExportAsService} from 'ngx-export-as';
import {ToastrService} from 'ngx-toastr';

import {AuthenticationService} from '../../../../../core/services/authentication.service';
// import {specification_status_value} from '../../../../../core/helpers/sale.helper';
import {DatatablesResponse} from '../../../../../core/helpers/datatables-response.helper';
import {environment} from '../../../../../../environments/environment';

import {DataTableDirective} from 'angular-datatables';
import {Subject} from 'rxjs';
import Swal from 'sweetalert2';

import 'datatables.net';
import 'datatables.net-bs4';
import {OptionsService} from '../../../../../core/services/options.service';
import {Router} from '@angular/router';
import {OptionsCreateComponent} from '../../components/options-create/options-create.component';
import {OptionsEditComponent} from '../../components/options-edit/options-edit.component';
import {PagesEditComponent} from '../../../../settings-web/components/pages-edit/pages-edit.component';
import {Options} from '../../../../../shared/models/options.model';

@Component({
    selector: 'app-options-lists',
    templateUrl: './options-lists.component.html',
    styleUrls: ['./options-lists.component.scss']
})
export class OptionsListsComponent implements OnInit {
    public modalRef: BsModalRef;
    @ViewChild(DataTableDirective, {static: true})
    dtElement: DataTableDirective;
    dtTrigger: Subject<any> = new Subject();
    dtOptions: any = {};
    optionsLang: any;
    moduletype: string;
    loginUser: any;
    options = [];
    settings: any;
    exportAsConfig: ExportAsConfig = {
        type: 'pdf',
        elementIdOrContent: 'specification_table',
    };
    modalConfigs = {
        animated: true,
        keyboard: true,
        backdrop: true,
        ignoreBackdropClick: false,
        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg'
    };
    private apiUrl = environment.apiUrl;

    constructor(
        public translate: TranslateService,
        private modalService: BsModalService,
        private exportAsService: ExportAsService,
        private http: HttpClient,
        private toastr: ToastrService,
        private router: Router,
        private optionsService: OptionsService,
        private authenticationService: AuthenticationService
    ) {
        this.authenticationService.loginUser.subscribe(x => this.loginUser = x);
    }

    ngOnInit() {
        switch (this.router.url) {
            case '/specifications':
                this.moduletype = 'specifications';
                this.optionsLang = this.translate.instant('specifications');
                break;
            case '/properties':
                this.moduletype = 'properties';
                this.optionsLang = this.translate.instant('properties');
                break;
            case '/manufacturers' :
                this.moduletype = 'manufacturers';
                this.optionsLang = this.translate.instant('manufacturers');
                break;
        }
        this.loadDatatable();
    }

    loadDatatable() {
        let that = this;
        this.dtOptions = {
            pagingType: 'full_numbers',
            pageLength: that.loginUser.settings.tables_pagination_limit,
            serverSide: true,
            processing: true,
            dom: '<"html5buttons"B>ltfrtip',
            data: [],
            order: [0],
            columns: [
                {
                    'sortable': true,
                    'width': '40%',
                    'target': [0]
                },
                {
                    'sortable': true,
                    'width': '40%',
                    'target': [1]
                },
                {
                    'sortable': true,
                    'width': '20%',
                    'target': [2]
                }
            ],
            buttons: [{
                extend: 'csv',
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('csv');
                }
            }, {
                extend: 'excel',
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('xlsx');
                }
            }, {
                extend: 'pdf',
                className: 'btn btn-datatable-gredient',
                action: function (e, dt, node, config) {
                    that.exportFiles('pdf');
                }
            }],
            language: {
                'sEmptyTable': this.translate.instant('common.datatable.sEmptyTable'),
                'sInfo': this.translate.instant('common.datatable.sInfo'),
                'sInfoEmpty': this.translate.instant('common.datatable.sInfoEmpty'),
                'sSearch': '',
                'sInfoPostFix': this.translate.instant('common.datatable.sInfoPostFix'),
                'sInfoThousands': this.translate.instant('common.datatable.sInfoThousands'),
                'sLengthMenu': this.translate.instant('common.datatable.sLengthMenu'),
                'sLoadingRecords': this.translate.instant('common.datatable.sLoadingRecords'),
                'sProcessing': this.translate.instant('common.datatable.sProcessing'),
                'sZeroRecords': this.translate.instant('common.datatable.sZeroRecords'),
                'sSearchPlaceholder': this.translate.instant('common.datatable.sSearchPlaceholder'),
                'oPaginate': {
                    'sFirst': this.translate.instant('common.datatable.oPaginate.sFirst'),
                    'sLast': this.translate.instant('common.datatable.oPaginate.sLast'),
                    'sNext': this.translate.instant('common.datatable.oPaginate.sNext'),
                    'sPrevious': this.translate.instant('common.datatable.oPaginate.sPrevious')
                },
                'oAria': {
                    'sSortAscending': this.translate.instant('common.datatable.oAria.sSortAscending'),
                    'sSortDescending': this.translate.instant('common.datatable.oAria.sSortDescending')
                }
            },
            ajax: (dataTablesParameters: any, callback) => {
                this.http.post<DatatablesResponse>(this.apiUrl + '/api/all-options', Object.assign(dataTablesParameters, {moduletype: this.moduletype}), {}).subscribe(resp => {
                    this.options = resp.data;
                    this.settings = resp;
                    this.settings = this.settings.settings;
                    callback({
                        recordsTotal: resp.recordsTotal,
                        recordsFiltered: resp.recordsFiltered,
                        data: [],
                    });
                });
            }
        };
    }

    exportFiles(type) {
        this.exportAsConfig.type = type;
        this.exportAsService.save(this.exportAsConfig, this.translate.instant('options.title')).subscribe(() => {
        });
    }

    ngAfterViewInit(): void {
        this.dtTrigger.next();
    }

    ngOnDestroy(): void {
        this.dtTrigger.unsubscribe();
    }

    rerender(): void {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
            dtInstance.destroy();
            setTimeout(() => {
                this.dtTrigger.next();

                if (this.options.length > 0) {
                    $('.tfoot_dt').addClass('d-none');
                } else {
                    $('.tfoot_dt').removeClass('d-none');
                }
            });
        });
    }

    deleteSpecification(id) {
        Swal.fire({
            title: this.translate.instant('common.swal.title'),
            text: this.translate.instant('common.swal.text') + this.optionsLang.title,
            type: 'warning',
            showCancelButton: true,
            confirmButtonText: this.translate.instant('common.swal.confirmButtonText'),
            cancelButtonText: this.translate.instant('common.swal.cancelButtonText')
        }).then((result) => {
            if (result.value) {
                this.optionsService.delete(id).subscribe(data => {
                    this.toastr.success(this.optionsLang.messages.delete, this.optionsLang.title);
                    this.rerender();
                });
            }
        });
    }

    getTranslateStatus(statusKey) {
        // return this.specificationStatusKeyValue[statusKey];
        return [];
    }

    openOptionsCreateModal() {
        this.modalRef = this.modalService.show(OptionsCreateComponent, this.modalConfigs);
        this.modalRef.content.event.subscribe(data => {
            this.rerender();
        });
    }

    openOptionsEditModal(specification) {
        this.optionsService.getById(specification.id)
            .subscribe(
                resp => {
                    let modalConfig = {
                        animated: true,
                        keyboard: true,
                        backdrop: true,
                        ignoreBackdropClick: false,
                        class: 'inmodal modal-dialog-centered animated fadeIn modal-lg',
                        initialState: {
                            options: resp
                        }
                    };
                    this.modalRef = this.modalService.show(OptionsEditComponent, modalConfig);
                    this.modalRef.content.event.subscribe(data => {
                        this.rerender();
                    });
            });
    }
}
