import {Component, EventEmitter, OnInit} from '@angular/core';
import {FormArray, FormBuilder, FormGroup, Validators} from '@angular/forms';
import {BsModalRef} from 'ngx-bootstrap/modal';
import {ToastrService} from 'ngx-toastr';
import {TranslateService} from '@ngx-translate/core';
import {Subject} from 'rxjs';
import {Router} from '@angular/router';

import {Options} from '../../../../../shared/models/options.model';

import {OptionsService} from '../../../../../core/services/options.service';
import {HelperService} from '../../../../../core/services/helper.service';

@Component({
    selector: 'app-options-edit',
    templateUrl: './options-edit.component.html',
    styleUrls: ['./options-edit.component.scss']
})

export class OptionsEditComponent implements OnInit {
    public event: EventEmitter<any> = new EventEmitter();
    public onClose: Subject<boolean>;
    editOptionsForm: FormGroup;
    formTables: any;
    selectboxOptions: any;
    moduletype: string;
    optionsLang: any;
    options: Options;
    isFormSubmitted = false;
    isPageLoaded = false;

    constructor(
        public translate: TranslateService,
        public bsModalRef: BsModalRef,
        private formBuilder: FormBuilder,
        private toastr: ToastrService,
        private optionsService: OptionsService,
        private router: Router,
        private helperService: HelperService
    ) {
    }

    get optionsControl() {
        return this.editOptionsForm.controls;
    }

    ngOnInit() {
        switch (this.router.url) {
            case '/specifications':
                this.moduletype = 'specifications';
                this.optionsLang = this.translate.instant('specifications');
                break;
            case '/properties':
                this.moduletype = 'properties';
                this.optionsLang = this.translate.instant('properties');
                break;
            case '/manufacturers' :
                this.moduletype = 'manufacturers';
                this.optionsLang = this.translate.instant('manufacturers');
                break;
        }
        this.onClose = new Subject();
        this.loadForms();
    }

    loadForms() {
        this.editOptionsForm = this.formBuilder.group({
            id: [this.options.id, Validators.required],
            name: [this.options.name, Validators.required],
            options_types_indentify: [this.options.options_types_indentify, Validators.required],
            description: [this.options.description],
            options: this.formBuilder.array([]),
            status: [true, Validators.required],
        });
        for (let valueOption of this.options.options) {
            let control = <FormArray>this.editOptionsForm.controls.options;
            control.push(
                this.formBuilder.group({
                    name: [valueOption.name, Validators.required],
                    id: [valueOption.id]
                })
            );
        }
        this.isPageLoaded = true;
    }

    addOption() {
        let control = <FormArray>this.editOptionsForm.controls.options;
        control.push(
            this.formBuilder.group({
                name: [null, Validators.required]
            })
        );
    }

    deleteOption(index) {
        let control = <FormArray>this.editOptionsForm.controls.options;
        control.removeAt(index);
    }

    onSubmit() {
        this.isFormSubmitted = true;

        if (this.editOptionsForm.invalid) {
            return;
        }

        this.optionsService.update(this.editOptionsForm.value)
            .subscribe(
                data => {
                    this.toastr.success(this.optionsLang.messages.update, this.optionsLang.title);
                    this.event.emit({data: true});
                    this.onCancel();
                });
    }

    onCancel() {
        this.onClose.next(false);
        this.bsModalRef.hide();
    }
}
