import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {NgxPermissionsGuard} from 'ngx-permissions';

import {OptionsListsComponent} from './pages/options-lists/options-lists.component';
import {OptionsCreateComponent} from './components/options-create/options-create.component';
import {OptionsEditComponent} from './components/options-edit/options-edit.component';
// import {SpecificationViewComponent} from './pages/specification-view/specification-view.component';

const routes: Routes = [{
    path: '',
    children: [
        {
            path: '',
            canActivate: [NgxPermissionsGuard],
            component: OptionsListsComponent
        },
        {
            path: 'create',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.create',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'options_create']
                }
            },
            component: OptionsCreateComponent
        },
        {
            path: 'edit/:id',
            canActivate: [NgxPermissionsGuard],
            data: {
                breadcrumbs: {
                    text: 'common.edit',
                    icon: 'fa fa-external-link',
                    show: true,
                    isHome: true
                },
                permissions: {
                    only: ['admin', 'super_admin', 'options_edit']
                }
            },
            component: OptionsEditComponent
        },
        // {
        //     path: 'detail/:id',
        //     canActivate: [NgxPermissionsGuard],
        //     data: {
        //         breadcrumbs: {
        //             text: 'common.detail',
        //             icon: 'fa fa-external-link'
        //         },
        //         permissions: {
        //             only: ['admin', 'super_admin', 'options_view']
        //         }
        //     },
        //     component: SpecificationViewComponent
        // }
    ]
}
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})

export class OptionsRoutingModule {
}
